@use 'base/variables' as v;
@use 'base/mixins' as mix;

.resumen {
    background-image: linear-gradient(to right, rgba(v.$azul,.7) , rgba(v.$azul,.7)), url(../img/bg_numeros.jpg);
    background-image: linear-gradient(to right, rgba(v.$azul,.7) , rgba(v.$azul,.7)), image-set(
        url(../img/bg_numeros.avif) 1x,
        url(../img/bg_numeros.webp) 1x,
        url(../img/bg_numeros.jpg) 1x,
    );

    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;

    padding: 12rem 0;

    &__grid {
        @include mix.contenedor;
        @include mix.grid(1,5rem);
        @include mix.tablet {
            @include mix.grid(2,5rem);
        }
    }

    &__bloque {
        text-align: center;
        color: v.$blanco;
    }

    &__texto {
        margin: 0;
        line-height: 1;
        font-size: 2.4rem;
        text-transform: uppercase;
        font-weight: v.$bold;

        &--numero {
            font-size: 8rem;
        }
    }
}