@use 'base/variables' as v;
@use 'base/mixins' as mix;

.agenda {
    padding-bottom: 5rem; 
    @include mix.contenedor();
}

.eventos {

    $modificador: &;
    &__heading {
        color: v.$azul;
        text-align: left;
        margin: 5rem 0;
        font-size: clamp(2rem, -0.8rem + 8vw , 6rem);
    }

    &__fecha {
        color: v.$gris;
        margin: 3rem 0 1rem 0;
    }

    &--workshops {
        #{$modificador}__heading {
            color: v.$secundario;
        }

        .evento__informacion {
            background-color: v.$secundario;
            
            &:hover {
                background-color: darken(v.$secundario, 10% );
            }
        }
    }

    .swiper-button-prev,
    .swiper-button-next {
        color: rgba(v.$blanco, .5);
    }
}

.evento {

    &__hora {
        font-weight: v.$bold;
    }

    &__informacion {
        background-color: v.$azul;
        color: v.$blanco;
        padding: 2rem;
        border-radius: 1rem;
        transition: background-color 300ms ease;

        &:hover{
            background-color: darken(v.$azul, 10%);
        }
    }

    &__nombre {
        font-size: 2rem;
        margin: 0;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
        overflow: hidden;
    }

    &__introduccion {
        display: -webkit-box;
        font-size: 1.6rem;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
    }

    &__autor-info {
        display: flex;
        gap: 2rem;
        align-items: center;
        justify-content: space-between;
    }

    &__imagen-autor{
        width: 5rem;
        border-radius: 50%;
        background-color: v.$blanco;
    }

    &__autor-nombre {
        font-weight: v.$bold;
    }
}