@use 'base/variables' as v;
@use 'base/mixins' as mix;

.paginacion {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 2.5rem;
    margin-bottom: 3rem;
    padding: 2rem;
    gap: 2rem;

    @include mix.tablet {
        justify-content: flex-end;
    }

    &__enlace {
        color: v.$azul;
        font-size: 1.6rem;

        &--texto {
            padding: 1rem;
            border-radius: .5rem;
            border: 1px solid v.$azul;

            &:hover {
                background-color: v.$azul;
                color: v.$blanco;
            }
        }

        &--actual {
            font-weight: v.$bold;
            color: v.$gris;
            display: none;

            @include mix.tablet {
                display: inline;
            }
        }

        &--numero {
            font-weight: v.$bold;
            display: none;

            @include mix.tablet {
                display: inline;
            }
        }


    }
}
