@use 'base/variables' as v;
@use 'base/mixins' as mix;

.horas {
    @include mix.reset-lista();
    @include mix.grid(1,1rem);

    @include mix.tablet {
        @include mix.grid(2,1rem);
    }

    &:hover {
        cursor: pointer;
    }

    &__hora{
        color: v.$azul;
        border: 1px solid v.$azul;
        border-radius: 1rem;
        padding: 1rem;
        text-align: center;

        &--seleccionada {
            @extend .horas__hora;
            background-color: v.$azul;
            color: v.$blanco;
        }

        &--deshabilitado {
            @extend .horas__hora;
            border: 1px solid v.$gris;
            color: v.$gris;
            opacity: .5;

            &:hover {
                cursor: not-allowed;
            }
        }
    }



}