@use 'base/variables' as v;
@use 'base/mixins' as mix;

.paquetes {

    &__grid{
        @include mix.contenedor();
        @include mix.grid(1, 3rem);
        @include mix.tablet {
            @include mix.grid(3,3rem);
        }
        align-items: flex-start;    
    }    
}

.paquete {
    @include mix.card;

    
    &:nth-child(2) {
        order: -1;
        
        @include mix.tablet {
            order: unset;
        }
    }

    &__lista {
        @include mix.reset-lista();
        line-height: 2;
    }

    &__precio {
        color: v.$azul;
        font-size: 6rem;
        font-weight: v.$black;
        text-align: center;
        margin: 3rem 0 0 0;
    }
}