@use 'base/variables' as v;
@use 'base/mixins' as mix;

.dashboard {
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: v.$gris-claro;

    &__header{
        background-color: v.$gris-oscuro;
        padding: 2rem;
        position: sticky;
    }

    &__header-grid {
        display: flex;
        flex-direction: column;
        @include mix.tablet {
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
        }
    }

    &__logo {
        @include mix.logo;
        @include mix.texto-fluido(2rem,3rem);
    }

    // &__nav{

    // }

    &__form {
        display: flex;
        justify-content: center;

    }

    &__submit--logout {
        @include mix.enlace;
    }

    &__grid {
        display: flex;
        flex: 1;
        overflow: auto;
    }

    &__sidebar{
        background-color: lighten(v.$gris-oscuro, 10%);
        width: 12rem;
    }

    &__menu {
        display: flex;
        flex-direction: column;
        padding: 1rem;
        gap: 1rem;
    }

    &__icono {
        font-size: 2rem;
    }

    &__menu-texto {
        font-size: 1.1rem;
        text-transform: uppercase;
        font-weight: v.$bold;
        margin-top: .5rem;
    }

    &__enlace {
        color: lighten(v.$gris-oscuro, 40%);
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 2rem;
        transition: all 300ms ease-in-out;
        
        &:hover,
        &--actual {
            background-color: lighten(v.$gris-oscuro, 20%);
            color: v.$blanco;
            border-radius: 1rem;
        }

    }

    &__contenido {
        padding: 1rem 1rem 3rem 1rem;
        width: 100%;
        overflow: auto;

        @include mix.tablet {
            padding: 1rem 1rem 3rem 5rem;
        }
    }

    &__contenedor-boton {
        @include mix.contenedor-md;
        display: flex;
        justify-content: flex-end;
        margin-bottom: 5rem;
    }

    &__boton {
        @include mix.boton(v.$azul, v.$blanco);
        font-size: 1.4rem;
        text-transform: uppercase;
        
        @include mix.tablet {
            width: auto;
        }
    }

    &__contenedor {
        @include mix.shadow;
        padding: 2rem;
        border-radius: 1rem;
        margin-top: 5rem;
        background-color: v.$blanco;
    }

    &__formulario {
        @extend .dashboard__contenedor;
        @include mix.contenedor-md;
    }


}