@use 'base/variables' as v;
@use 'base/mixins' as mix;

.table {
    width: 100%;
    border-collapse: collapse;

    &__thead{
        background-color: v.$gris_oscuro;
        color: v.$blanco;
    }    

    &__th {
        padding: 1rem;
        text-align: left;
    }

    &__tr{
        &:nth-of-type(even){
            background-color: darken(v.$gris_claro, 5%);
        }
    }

    &__td {
        padding: 1.5rem 1rem;

        &--acciones {
            @extend .table__td;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            gap: 2rem;
        }
    }

    &__accion {
        all: unset;

        &--editar {
            color: v.$azul;
        }

        &--eliminar {
            color: v.$rojo;
        }

        &:hover{
            cursor: pointer;
        }
    }
}