@use 'variables' as v;
@use 'mixins' as mix;

h1, h2, h3 {
    @include mix.headings;
}

h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
}
.text-center {
    text-align: center;
}

[class$='__heading'] {
    @include mix.nombre-pagina;
}

[class$='__descripcion'] {
    @include mix.descripcion-pagina;
}